#top .test {
  content: "";
  position: relative;
  top: 0;
  left: 0;
  z-index: 11;
  box-sizing: border-box;
  display: grid;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  float: none;
  width: 100%;
  min-width: none;
  max-width: none;
  height: auto;
  min-height: auto;
  max-height: none;
  padding: 0;
  margin: 0;
  overflow: auto;
  border: 1px solid #f00;
  border-radius: none;
  background: linear-gradient(to right, #ff6f00 50%, #009688 50%);
  font-size: 15px;
  font-weight: 900;
  line-height: normal;
  color: #fff;
  text-align: center;
  text-decoration: none;
  letter-spacing: normal;
  cursor: pointer;
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  transition: all;
}

